// @content.sass

// javascirpt add class
.deemed-bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6); }

// layer 숨김
.layer-container {
	position: absolute;
	top: -100%;
	left: -100%; }

// 클릭 후 해당 위치
.layer-container-pop {
	overflow: hidden;
	position: absolute;
	top: 50%;
	left: 50%;
	width: rem(960px);
	height: rem(500px);
	border-radius: rem(5px);
	transform: translate(-50%, -50%);
	box-shadow: 0 4px 6px rgba(0,0,0,.25);
	background: $color-white;
	z-index: 1000; }

#main {
	.goods-title {
		text-align: center;
		@include font-size(20px);
		font-weight: 100;
		transition: all 0.3s ease-out;
		margin: {
			bottom: rem(32px); }
		@include font-size(37px);
		.goods-small-title {
			@include font-size(37px);
			font-weight: 100; } }

	.goods-tab-list {
		margin: {
			bottom: rem(30px); }
		text-align: center;
		li {
			display: inline-block;
			a {
				padding: {
					left: rem(20px);
					right: rem(20px); }
				@include font-size(20px);
				color: $color-black; } }
		&::after {
			@extend %clearfix; } }

	.goods-tab-list-fixed {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		padding: {
			top: rem(20px);
			bottom: rem(20px); }
		border: {
			bottom: 1px solid $color-tab-bottom; }
		background: $color-white;
		box-shadow: 0 1px 100px rgba(0,0,0,0.1);
		transition: all 0.2s ease-out; }

	.goods-tab-contents {
		margin: {
			left: auto;
			right: auto; }
		// width: rem(1200px)

		.tab-fun-list-contanier {
			.tab-fun-list {
				min-width: rem(1000px);
				li {
					box-sizing: border-box;
					float: left;
					width: 20%;
					border: 1px solid $color-white;
					background: rgba(0,0,0,0.0274);
					// background: #ccc
					.goods-like-content {
						margin: {
							top: rem(20px);
							left: rem(20px); }
						.goods-like-heart {
							@extend .sprites !optional;
							@extend %ir !optional;
							@extend .sprites__amazonHeart !optional;
							float: left;
							padding-left: rem(10px); }

						.goods-like-count {
							float: left;
							@include font-size(12px);
							color: #b6b9b6; }
						&::after {
							@extend %clearfix; } }

					.goods-fun-content {
						a {
							display: block;
							width: rem(167px);
							margin: {
								left: rem(32px); } } }
					.goods-price-content {
						padding: {
							bottom: rem(10px); }
						.goods-price {
							// display: block
							margin: {
								left: rem(200px);
								bottom: rem(20px); }
							@include font-size(16px);
							color: $goods-price-color; } }

					// layer팝업
					.layer-container {
						.content-container {
							position: relative;
							width: rem(960px);
							.carousel-container {
								overflow: hidden;
								float: left;
								width: 50%;
								height: rem(500px);
								// background: #eee

								.carousel-controls {
									position: relative;
									height: 100%;
									// background: red

									.carousel-indicator {
										position: absolute;
										left: 50%;
										bottom: rem(25px);
										transform: translateX(-50%);
										z-index: 200;
										button {
											@extend %ir;
											@extend %button;
											float: left;
											width: rem(12px);
											height: rem(12px);
											margin: {
												left: rem(5px);
												right: rem(5px); }
											border-radius: 50%;
											background: #696969;
											transition: all 0.3s ease-out;
											&::focus {
												outline: none;
												outline-offset: rem(3px);
												box-shadow: 0 0 0 4px hsla(198, 82%, 47%, 0.32); } }

										.active-tab {
											background: $color-white;
											outline-offset: rem(3px);
											box-shadow: 0 0 0 3px hsla(0, 0%, 27%, 0.25); }

										&::after {
											@extend %clearfix; } }

									.carousel-navigator {
										button {
											@extend %ir;
											@extend %button;
											position: absolute;
											top: 50%;
											transform: translateY(-50%);
											width: 34px;
											height: 34px;
											background: #212121;
											border-radius: 50%;
											z-index: 200;
 }											// hover, focus 처리하기

										.carousel-previous-btn, .carousel-next-btn {
											&::after {
												content: '';
												position: absolute;
												top: 50%;
												display: block;
												width: 8px;
												height: 8px;
												opacity: 0.7;
												transform: translate(-50%, -50%) rotate(-45deg);
												transition: all .4s ease-out; } }

										.carousel-previous-btn {
											left: rem(15px);
											&::after {
												left: 53%;
												border: {
													top: 2px solid $color-white;
													left: 2px solid $color-white; } } }
										.carousel-next-btn {
											right: rem(15px);
											&::after {
												left: 47%;
												border: {
													right: 2px solid $color-white;
													bottom: 2px solid $color-white; } } } }

									.carousel-view {
										overflow: hidden;
										img {
											float: left;
											@extend %remove-img-space; } } } }

							.goods-container {
								float: left;
								width: 50%;
								// height: 500px
								// background: #ccc
								z-index: 200; }
							&::after {
								@extend %clearfix; }

							.goods-close {
								position: absolute;
								top: rem(10px);
								right: rem(10px);
								.goods-close-button {
									@extend %button;
									width: rem(20px);
									height: rem(20px);
									background: none;
									span {
										@extend %ir; } }
								&::before,
								&::after {
									content: '';
									position: absolute;
									top: 50%;
									left: 0;
									width: rem(20px);
									height: rem(2px);
									background: $header-bg-color;
									transition: all 0.3s; }

								&::before {
									transform: rotate(45deg); }
								&::after {
									transform: rotate(-45deg); } } } } }
				&::after {
					@extend %clearfix; } } } } }























