// @header

.wrapper {}

.header {
	// width: rem(1920px)
	min-width: rem(1000px);
	margin: {
		left: auto;
		right: auto;
		top: 0;
		bottom: rem(55px); }

	.header-top {
		box-sizing: border-box;
		background: $header-bg-color;
		height: rem(80px);
		.header-logo {
			position: relative;
			float: left;
			width: 14%;
			margin: {
				top: rem(30px);
				left: rem(30px); }

			.logo-link {
				display: block;
				.logo-amanzom {
					@extend .sprites !optional;
					@extend %ir !optional;
					@extend .sprites__amazonLogo !optional; } }
			.logo-try-prime {
				position: absolute;
				top: rem(18px);
				left: rem(68px);
				color: #48A3C6;
				font-size: rem(11px);
				text-decoration: underline;
				white-space: nowrap;
				font-weight: 700; } }

		//검색
		.header-search {
			float: left;
			width: 56%;
			margin: {
				top: rem(20px);
 }				// left: rem(80px)

			.search-form {
				width: rem(700px);
				height: rem(50px);
				.search-field {
					position: relative;
					float: left;
					width: rem(600px);
					height: rem(40px);

					.search-input {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						line-height: rem(15px);
						margin: 0;
						border: 0;
						font-size: rem(15px);
						color: $input-color;
						outline: 0;
						text-indent: rem(8px);
						border-radius: rem(5px) 0 0 rem(5px); } }

				.search-button-box {
					float: left;
					width: rem(45px);

					.search-button {
						@extend %button;
						width: rem(45px);
						height: rem(42px);
						border-radius: 0 rem(5px) rem(5px) 0;
						background: $search-button-color;
						span {
							@extend .sprites !optional;
							@extend %ir !optional;
							@extend .sprites__amazonSearch !optional;
							margin: {
								left: rem(6px); } } } }
				&::after {
					@extend %clearfix; } } }

		.header-banner {
			float: left;
			width: 26%;
			margin: {
				top: rem(20px); } }
		&::after {
			@extend %clearfix !optional; } }

	.header-bottom {
		box-sizing: border-box;
		height: rem(53px);
		background: $header-bg-color;
		.nav-left {
			float: left;
			width: 30%;
			color: #fff;
			.menu-left-1depth {
				display: block;
				margin: {
					top: rem(5px);
					left: rem(30px); }
				@include font-size(15px);
				font-weight: 700;
				color: $color-white; }
			.menu-left-2depth {
				display: none; } }

		.nav-right {
			@extend .nav-left;
			width: 40%;
			color: #fff; }
		.nav-center {
			@extend .nav-left;
			width: 30%;
			color: #fff; }

		&::after {
			@extend %clearfix; } } }















